.urls {
  width: 100%;

  margin-top: 18px;
  margin-bottom: 12px;
  text-align: center;
}

.urls .url--form {
  width: 80%;

  margin: auto;
  padding: 16px 0px;
  text-align: center;

  display: -ms-flexbox;

  display: flex;
  -ms-flex-pack: center;
      justify-content: center;

  background-color: #f5f5f5;
}

.urls .url--form .new-url {
  width: auto;
  border: solid 1px #949494;
  padding: 5px;

  margin: 0px 16px;

  font-family: 'Roboto';
  font-size: 16px;
}

.urls .url--form .submit-url {
  height: 32px;
  width: 32px;

  border: none;
  border-radius: 999px;

  margin: 0px 16px;

  background-color: #2CEAA3;
  color: #FEFEFE;
}

.urls hr {
  width: 80%;
  height: 2px;
  margin: 18px auto 18px;
  border: none;

  background-color: #656565;
}

.sort-options button {
  padding: 4px 6px;
  margin: 0px 8px;

  border: none;
  border-radius: 999px;

  font-family: 'Roboto';
  font-size: 16px;

  background-color: #f5f5f5;
}

.sort-options .active {
  background-color: #adadad;
}

.urls .url-list {
  width: 80%;

  margin: 16px auto;
  padding: 0;

  list-style: none;
}

@media (min-width:961px)  {
 /* tablet, landscape iPad, lo-res laptops ands desktops */
 .dashboard {
   width: 960px;
 }

 .urls .url-list {
   display: grid;
   grid-template-columns: 50% 50%;
 }

 .urls .url--form .new-url {
   width: 350px;
 }

}



