.url {
  height: 130px;
  background-color: #f5f5f5;

  display: -ms-flexbox;

  display: flex;
  -ms-flex-align: center;
      align-items: center;

  position: relative;

  overflow: hidden;

  font-family: 'Roboto';
  font-size: 16px;
}

.url:nth-child(odd) {
  background-color: #FEFEFE;
}

.url .data {
  height: 100%;
  width: 100%;
}

.url p {
  margin: 16px 0px 0px 16px;
  text-align: left;
  white-space: nowrap;
}

.url .fade {
  height: 100%;
  width: 90px;

  position: absolute;
  right: 0;

  background: -webkit-gradient(linear, left top, right top, from(rgba(255, 255, 255, 0)), color-stop(30%, #FEFEFE));

  background: -webkit-linear-gradient(left, rgba(255, 255, 255, 0) 0%, #FEFEFE 30%);

  background: -o-linear-gradient(left, rgba(255, 255, 255, 0) 0%, #FEFEFE 30%);

  background: linear-gradient(to right, rgba(255, 255, 255, 0) 0%, #FEFEFE 30%);
}

.url:nth-child(even) .fade {
  background: -webkit-gradient(linear, left top, right top, from(rgba(255, 255, 255, 0)), color-stop(30%, #f5f5f5));
  background: -webkit-linear-gradient(left, rgba(255, 255, 255, 0) 0%, #f5f5f5 30%);
  background: -o-linear-gradient(left, rgba(255, 255, 255, 0) 0%, #f5f5f5 30%);
  background: linear-gradient(to right, rgba(255, 255, 255, 0) 0%, #f5f5f5 30%);
}

.url button {
  height: 26px;
  width: 26px;

  border: none;
  border-radius: 999px;

  position: absolute;
  right: 16px;
  top: 50%;
  -webkit-transform: translateY(-50%);
      -ms-transform: translateY(-50%);
          transform: translateY(-50%);

  background-color: #EF4135;
}

@media (min-width:961px) {
  /* produces a checkered pattern on desktop view */
  .url:nth-child(4n+1), .url:nth-child(4n+4) {
    background-color: #fefefe;
  }

  .url:nth-child(4n+2), .url:nth-child(4n+3) {
    background-color: #f5f5f5;
  }

  .url:nth-child(4n+1) .fade, .url:nth-child(4n+4) .fade {
    background: -webkit-gradient(linear, left top, right top, from(rgba(255, 255, 255, 0)), color-stop(30%, #fefefe));
    background: -webkit-linear-gradient(left, rgba(255, 255, 255, 0) 0%, #fefefe 30%);
    background: -o-linear-gradient(left, rgba(255, 255, 255, 0) 0%, #fefefe 30%);
    background: linear-gradient(to right, rgba(255, 255, 255, 0) 0%, #fefefe 30%);
  }

  .url:nth-child(4n+2) .fade, .url:nth-child(4n+3) .fade {
    background: -webkit-gradient(linear, left top, right top, from(rgba(255, 255, 255, 0)), color-stop(30%, #f5f5f5));
    background: -webkit-linear-gradient(left, rgba(255, 255, 255, 0) 0%, #f5f5f5 30%);
    background: -o-linear-gradient(left, rgba(255, 255, 255, 0) 0%, #f5f5f5 30%);
    background: linear-gradient(to right, rgba(255, 255, 255, 0) 0%, #f5f5f5 30%);
  }
}



